
import { Exam } from '@/types'
import { defineComponent } from 'vue'
import examMixin from '@/mixins/exam'

export default defineComponent({
  name: 'WeekExam',
  mixins: [examMixin],
  props: {
    exam: {
      type: Object as () => Exam,
      required: true
    }
  },
  computed: {
    locked (): boolean {
      return this.examLocked(this.exam) !== 0
    }
  }
})
